import React from "react";

import * as ST from "./styled";

const Error = () => (
	<ST.Error>
		<ST.ErrText> </ST.ErrText>
		<ST.Btns>
			{window.location.pathname !== "/" && <ST.Btn onClick={() => window.location.href = '/'}> </ST.Btn>}
            <ST.Btn onClick={() => window.location.reload()}></ST.Btn>
		</ST.Btns>  
	</ST.Error>
);

export default Error;
